@charset "UTF-8";
* {
  box-sizing: border-box; }

body {
  margin: 0;
  padding: 0;
  min-width: 1440px;
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 300;
  font-size: 16px;
  line-height: 32px;
  color: #333333; }

.visually-hidden {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden; }

.container {
  width: 1380px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px; }

.page {
  display: grid;
  grid-template-rows: auto 1fr auto;
  grid-template-areas: "header" "content" "footer";
  min-height: 100vh; }

.header {
  grid-area: header;
  background-color: #f2f8fb; }

.page__content {
  grid-area: content; }

.footer {
  grid-area: footer;
  background-color: #f2f8fb; }

.page__link {
  text-decoration: none;
  font-weight: normal;
  font-size: 16px;
  line-height: 20px;
  color: #06a9b3; }

.button {
  display: inline-block;
  padding: 16px 30px;
  font-family: inherit;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  color: #ffffff;
  background-color: #06a9b3;
  text-decoration: none;
  border: none; }

.page__content h1 {
  margin: 0;
  font-size: 44px;
  line-height: 62px;
  color: #333333; }

.title {
  position: relative;
  margin: 0;
  padding: 0 0 0 115px;
  font-weight: normal;
  font-size: 16px;
  line-height: 20px;
  color: #06a9a3; }

.title::before {
  counter-increment: section-counter;
  /* Инкрементирует счетчик*/
  content: "0" counter(section-counter);
  /* Отображает текущее значение счетчика */
  position: absolute;
  left: 0;
  top: 0;
  font-weight: 300;
  font-size: 16px;
  line-height: 20px;
  color: #06a9b3; }

.title::after {
  content: "";
  position: absolute;
  width: 81px;
  height: 1px;
  left: 25px;
  top: 9px;
  border-top: 1px solid #06a9b3; }

.headline {
  margin: 60px 0 25px 0;
  font-size: 32px;
  line-height: 48px; }

.header__nav {
  display: grid;
  grid-template-columns: auto 1fr auto auto;
  grid-column-gap: 65px; }

.header__list {
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  list-style: none; }

.header__item {
  padding-right: 65px; }

.header__item:last-child {
  padding-right: 0; }

.header__container--logo {
  padding: 20px 110px 20px 0;
  text-align: center; }

.header__container--search {
  padding: 25px 0 23px; }

.header__search {
  width: 16px;
  height: 16px;
  display: block;
  padding: 7px 0;
  background-image: url("/img/search_icon.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 16px 16px; }

.header__user {
  padding: 25px 0 23px; }

.welcome {
  padding-bottom: 100px;
  background-color: #f2f8fb; }

.welcome__content {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: repeat(5, auto);
  grid-column-gap: 24px; }

.welcome__city {
  grid-area: 1 / 2 / 2 / 8;
  margin: 80px 0 10px 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: #aaaaaa; }

.welcome h1 {
  grid-area: 2 / 2 / 3 / 8;
  z-index: 1; }

.welcome__text {
  grid-area: 3 / 2 / 4 / 8;
  z-index: 1;
  margin: 10px 0 40px 0;
  font-weight: 400;
  font-size: 22px;
  line-height: 34px;
  color: #333333; }

.welcome__button {
  grid-area: 4 / 2 / 5 / 4;
  margin-bottom: 90px; }

.welcome__pic {
  grid-area: 1 / 6 / -1 / -1; }

.welcome__img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover; }

.welcome__list {
  grid-area: 5 / 2 / 6 / 5;
  padding: 0;
  margin: 10px 0 0 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  align-items: center; }

.welcome__item {
  margin-right: 73px; }

.welcome__item:last-child {
  margin-right: 0; }

.welcome__link {
  display: block;
  width: 44px;
  height: 44px; }

.welcome__link--facebook {
  background-image: url("/img/fb_icon.svg");
  background-repeat: no-repeat;
  background-position: center; }

.welcome__link--instagram {
  background-image: url("/img/ins_icon.svg");
  background-repeat: no-repeat;
  background-position: center; }

.welcome__link--vk {
  background-image: url("/img/vk_icon.svg");
  background-repeat: no-repeat;
  background-position: center; }

.welcome__controls {
  grid-area: 5 / 6 / 6 / -1;
  display: grid;
  grid-template-columns: auto auto 1fr; }

.welcome__btn {
  width: 105px;
  height: 66px;
  padding: 0;
  border: none;
  background-color: #ffffff; }

.welcome__btn--left {
  background-image: url("/img/arw_left_icon.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 30px 16px; }

.welcome__btn--right {
  background-image: url("/img/arw_right_icon.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 30px 16px; }

.welcome__page {
  margin: 10px 30px 0 0;
  text-align: right;
  font-weight: normal;
  font-size: 32px;
  line-height: 48px;
  color: #ffffff; }

.promo {
  position: relative;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  column-gap: 24px;
  margin-top: 100px;
  margin-bottom: 95px; }

.promo::before {
  content: "01";
  position: absolute;
  left: 145px;
  top: 0;
  font-weight: 900;
  font-size: 65px;
  line-height: 65px;
  color: #eaf2f6; }

.promo::after {
  content: "Место";
  position: absolute;
  left: 145px;
  top: 60px;
  font-weight: 900;
  font-size: 65px;
  line-height: 65px;
  color: #eaf2f6;
  writing-mode: vertical-lr;
  transform: scale(-1, -1) translateY(-40px); }

.promo__header {
  grid-column: 5 / 9; }

.promo__content {
  grid-column: 1 / -1;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  column-gap: 24px; }

.promo__content > * {
  grid-column: 5 / 9; }

.promo__text {
  margin-top: 0;
  margin-bottom: 25px; }

.promo__img {
  margin: 0 0 25px 0; }

.promo__img--big {
  grid-column: 2 / -2; }

.promo__blockquote {
  grid-column: 4 / 10;
  margin: 0 0 35px 0;
  padding-left: 30px;
  font-weight: normal;
  font-size: 22px;
  line-height: 40px;
  border-left: 1px solid #06a9b3; }

.promo__btn {
  grid-column: 5 / 8;
  margin-top: 10px; }

.promo__text--right {
  grid-column: -4 / -2;
  font-size: 14px;
  line-height: 24px; }

.promo__text--left {
  grid-column: 2 / 4;
  margin-top: 20px;
  font-size: 14px;
  line-height: 24px; }

.catalog {
  padding: 0 0 140px 0;
  background-color: #f2f8fb; }

.catalog__content {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 24px; }

.catalog__item {
  grid-column: span 3;
  border: 1px solid #c4c4c4;
  display: grid;
  grid-template-columns: 3fr;
  grid-template-rows: 220px auto;
  grid-gap: 17px 24px;
  background-color: #ffffff; }

.catalog__item--horizontal {
  grid-column: 3 / -3;
  grid-template-columns: 3fr 5fr;
  grid-template-rows: auto; }

.catalog__text {
  padding: 0 18px 23px; }

.catalog__text--horizontal {
  padding: 20px 24px 24px 0; }

.catalog__pic {
  width: 100%;
  height: 100%;
  object-fit: cover; }

.catalog__title {
  margin: 0 0 5px 0;
  font-weight: normal;
  font-size: 22px;
  line-height: 34px;
  color: #333333; }

.catalog__para {
  margin: 0 0 17px 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #aaaaaa; }

.catalog__text dl {
  margin: 0;
  padding: 15px 0 0 0;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-column-gap: 50px;
  border-top: 2px solid #eaf2f6; }

.catalog__text--horizontal dl {
  grid-column-gap: 175px; }

.catalog__text dt {
  margin: 0;
  font-weight: 300;
  font-size: 16px;
  line-height: 20px; }

.catalog__text dd {
  margin: 0;
  grid-row: 1 / 2;
  font-weight: 300;
  font-size: 32px;
  line-height: 36px; }
